// src/pages/success.tsx
import { GetServerSideProps } from "next";
import Stripe from "stripe";
import Link from "next/link";
import prisma from "@/lib/prisma";

interface SessionItem {
  dateISO: string;
  hourRaw: string;
  therapistName: string;
  calLink: string;
  label: string;
}

interface SuccessProps {
  items: SessionItem[];
}

export const getServerSideProps: GetServerSideProps<SuccessProps> = async ({
  query,
}) => {
  const sessionId = query.session_id as string | undefined;
  if (!sessionId) {
    return {
      redirect: {
        destination: "/dashboard?tab=reservar",
        permanent: false,
      },
    };
  }

  const stripe = new Stripe(process.env.STRIPE_SECRET!, {
    apiVersion: "2025-04-30.basil",
  });
  const session = await stripe.checkout.sessions.retrieve(sessionId);
  const m = session.metadata ?? {};

  // Parse arrays from metadata
  const datesArr = JSON.parse(m.dates as string) as string[];
  const hoursArr = JSON.parse(m.hours as string) as string[];
  const therapistsArr = JSON.parse(m.therapists as string) as string[];
  const servicioSlug = m.servicio as string;

  const serviceNames: Record<string, string> = {
    agua: "Estimulación en agua",
    piso: "Estimulación en piso",
    quiropractica: "Quiropráctica",
    fisioterapia: "Fisioterapia",
    masajes: "Masajes",
    cosmetologia: "Cosmetología",
    "prevencion-lesiones": "Prevención de lesiones",
    "preparacion-fisica": "Preparación física",
    nutricion: "Nutrición",
    "medicina-rehabilitacion": "Medicina en rehabilitación",
    "terpia-post-vacuna": "Terapia post vacuna",
  };
  const servicioLabel =
    serviceNames[servicioSlug] ?? `Cita de ${servicioSlug}`;

  // For each session, persist reservation in the database
  for (let i = 0; i < datesArr.length; i++) {
    const dateISO = datesArr[i];
    const [h = 0, mnt = 0] = (hoursArr[i] || "0:00")
      .split(":")
      .map((v) => Number(v) || 0);
    const therapistName = therapistsArr[i];

    // Find the service and therapist records by name
    const svc = await prisma.service.findFirst({
      where: { name: servicioLabel },
    });
    const ther = await prisma.therapist.findFirst({
      where: { name: therapistName },
    });

    if (svc && ther) {
      const dt = new Date(dateISO);
      dt.setHours(h, mnt, 0);
      await prisma.reservation.create({
        data: {
          userId: session.metadata?.userId as string,
          serviceId: svc.id,
          therapistId: ther.id,
          date: dt,
        },
      });
    }
  }

  // Build calendar links for UI
  const items: SessionItem[] = datesArr.map((dateISO, i) => {
    const [h = 0, mnt = 0] = (hoursArr[i] || "0:00")
      .split(":")
      .map((v) => Number(v) || 0);
    const start = new Date(dateISO);
    start.setHours(h, mnt, 0);
    const end = new Date(start.getTime() + 60 * 60 * 1000);
    const fmt = (d: Date) => d.toISOString().replace(/[-:]|\.\d{3}/g, "");
    const dates = `${fmt(start)}/${fmt(end)}`;
    const text = encodeURIComponent(servicioLabel);
    const details = encodeURIComponent(`Terapeuta: ${therapistsArr[i]}`);
    const location = encodeURIComponent("Bloom Fisio");
    const calLink = [
      "https://www.google.com/calendar/render?action=TEMPLATE",
      `&text=${text}`,
      `&dates=${dates}`,
      `&details=${details}`,
      `&location=${location}`,
    ].join("");

    const label = `${start.toLocaleDateString()} • ${start.toLocaleTimeString(
      [],
      { hour: "2-digit", minute: "2-digit" }
    )}`;

    return {
      dateISO,
      hourRaw: hoursArr[i],
      therapistName: therapistsArr[i],
      calLink,
      label,
    };
  });

  return { props: { items } };
};

export default function Success({ items }: SuccessProps) {
  return (
    <div className="text-center py-5">
      <h1>¡Gracias por tu pago!</h1>
      <p>Ahora puedes agregar cada sesión a tu Google Calendar:</p>

      {items.map((item, idx) => (
        <div key={idx} className="mb-4">
          <p>
            <strong>Sesión {idx + 1}:</strong> {item.label} — {item.therapistName}
          </p>
          <a
            href={item.calLink}
            target="_blank"
            rel="noopener noreferrer"
            className="btn btn-orange me-2 mb-2"
          >
            ➕ Agregar sesión al Calendario {idx + 1}
          </a>
        </div>
      ))}

      <Link href="/dashboard?tab=historial" legacyBehavior>
        <button className="btn btn-orange">Ver Historial</button>
      </Link>
    </div>
  );
}